.add-list {
    &__popup {
        position: relative;
        top: -25px;
        left: 10px;
        height: 150px;
        width: 240px;
        background: #ffffff;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.05);
        border-radius: 10px;
        padding: 18px;
        box-sizing: border-box;

        .button {
            width: 100%;
            margin-top: 12px;
        }

        &-close-btn {
            position: absolute;
            top: -8px;
            right: -8px;
            cursor: pointer;
        }

        &-colors {
            display: flex;
            justify-content: space-between;
            margin-top: 12px;

            .badge {
                width: 16px;
                height: 16px;
                cursor: pointer;
                border: 2px solid transparent;

                &.active {
                    border: 2px solid #525252;
                }
            }
        }
    }
}
